import { Link, graphql, useStaticQuery } from "gatsby";
import React from "react";
import styled from "styled-components";
import sowoojooLogo from "../images/sowoojooLogo.png";

const LayoutWrapper = styled.div`
  display: flex;
  flex-direction: column;
  height: auto; /*      */
`;

const Header = styled.header`
  color: black;
  padding: 1rem;
  text-align: center;
  height: 200px;
`;

const Footer = styled.footer`
  margin-top: 2rem;
  background-color: rgb(150, 150, 150);
  color: white;
  padding: 1rem;
  text-align: center;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Main = styled.main`
  flex: 1; /*    */
  padding: 0 400px;
  min-height: calc(
    100vh - 300px - 2rem
  ); /* 100vh Header(200px) Footer(100px)   */
  overflow-y: auto;
`;

const Title = styled(Link)`
  font-size: 2rem;
`;

const Img = styled.img`
  width: 200px;
`;

const Nav = styled.nav`
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
`;

const NavLink = styled(Link)`
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
  height: 40px;
  width: 120px;
  border-radius: 20px;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);
  text-align: center;

  color: rgb(242, 167, 59);
  &:hover {
    color: white;
    background-color: rgb(242, 167, 59);
  }
`;

const Dropdown = styled.div`
  position: relative;
  display: inline-block;

  &:hover > div {
    display: block; /*      */
  }
`;

const DropdownContent = styled.div`
  display: none; /*   */
  position: absolute;
  background-color: white;
  color: black;
  min-width: 120px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  z-index: 1;
  top: 100%; /*     */
  left: 0;

  a {
    display: block;
    padding: 10px 16px;
    color: rgb(242, 167, 59);
    text-decoration: none;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 4px;

    &:hover {
      background-color: rgb(242, 167, 59);
      color: white;
    }
  }
`;

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query {
      markdownRemark(fileAbsolutePath: { regex: "/content/footer/" }) {
        html
      }
    }
  `);

  const footerContent =
    data?.markdownRemark?.html || "<p>Footer  .</p>"; //      

  return (
    <LayoutWrapper>
      <Header>
        <Title to="/">
          <Img src={sowoojooLogo} alt="" />
        </Title>
        <Nav>
          <Dropdown>
            <NavLink to="/about/info"></NavLink>
            <DropdownContent>
              <Link to="/about/info"></Link>
              <Link to="/about/history"></Link>
            </DropdownContent>
          </Dropdown>
          <Dropdown>
            <NavLink to="/programs/lecture"> </NavLink>
            <DropdownContent>
              <Link to="/programs/lecture"></Link>
              <Link to="/programs/experience"></Link>
              <Link to="/programs/puppet-show"></Link>
            </DropdownContent>
          </Dropdown>
          <NavLink to="/activities"></NavLink>
          <NavLink to="/notice"></NavLink>
          <NavLink to="/map"> </NavLink> {/*   */}
        </Nav>
      </Header>
      <Main>{children}</Main>
      <Footer>
        <div dangerouslySetInnerHTML={{ __html: footerContent }} />
      </Footer>
    </LayoutWrapper>
  );
};

export default Layout;
